.AppHeader {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: flex-end;
	        align-items: flex-end
}

.AppHeader h1 {
	font-size: 42px;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.AppHeader h2 {
	font-size: 18px;
	color: darkgrey;
}
.SearchPanel_input {
	margin-right: 7px;
	width: auto;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.TodoListItem {
	cursor: pointer;
	font-size: 20px;
	line-height: 30px
}

.TodoListItemLabel {
	margin-left: 20px;
	line-height: 40px;
}

.TodoListItem.done {
	text-decoration: line-through
}

.TodoListItem.important .TodoListItemLabel {
	color: steelblue;
	font-weight: bold;
}

.TodoListItem button {
	width: 35px;
	height: 35px;
	margin: 3px;
}
.TodoList	{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.TodoList .list-group {
	margin-bottom: 0;
	padding-left: 0;
}

.TodoList .list-group-item  {
	padding: 5px 5px;
}
.TodoAddItem {
		margin-top: 10px;
}

.TodoAddItem input[type="text"] {
	margin-right: 7px;
}

.TodoAddItem .js-hide {
	display: none;
	
}

.TodoAddItem .js-show {
	opacity: 1;
}

.TodoAddItem .title-warning {
	font-size: 18px;
	transition: .5s ease-in opacity;
	color: darkred;
	text-align: center;	
	padding: 10px;
}
.App {
	max-width: 400px;
	margin: 20px auto;
}

.top-panel {
  margin: 1rem 0;
}

